import React from "react"
import { FormItem, Radio } from "formik-antd"
import styled from "styled-components"
import { Form, Radio as AntdRadio } from "antd"

const StyledRadioGroup = styled(Radio.Group)`
	> label {
		display: ${({ labelBlock }) => labelBlock && "block"};
	}
`

function RadioInput({ options, name, label, independent, isButton = true, ...props }) {
	if (independent) {
		return (
			<Form.Item name={name} label={label}>
				<AntdRadio.Group {...props} name={name} buttonStyle="solid">
					{options.map(item => (
						<AntdRadio.Button value={item.value} key={item.value}>
							{item.label}
						</AntdRadio.Button>
					))}
				</AntdRadio.Group>
			</Form.Item>
		)
	}

	return (
		<FormItem name={name} label={label}>
			{isButton ? (
				<StyledRadioGroup {...props} name={name} buttonStyle="solid">
					{options.map(item => (
						<Radio.Button value={item.value} key={item.value}>
							{item.label}
						</Radio.Button>
					))}
				</StyledRadioGroup>
			) : (
				<StyledRadioGroup {...props} options={options} name={name} />
			)}
		</FormItem>
	)
}

export default RadioInput
