import React, { useEffect, useState } from "react"
import { withRouter, useHistory } from "react-router"
import { Layout, Section, Heading, Loading } from "components"
import { Row, Col, Tabs, Form, message, Icon, Modal } from "antd"
import styled from "styled-components"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { useMutation, useQuery, useLazyQuery } from "react-apollo"
import { mutateUserProfile, mutateUpdateProfile, mutateUpdateCurriculum } from "queries/profile"
import { mobile, newClient, renderError } from "helpers"
import { SubmitButton, DatePicker, FormItem } from "formik-antd"
import moment from "moment"
import RadioInput from "components/forms/RadioInput"
import { mutateUpdatePassword, MUTATE_DEACTIVATE_ACCOUNT, queryLogout } from "queries/auth"
import SelectInput from "components/forms/SelectInput"
import { queryFetchLevels } from "queries/data"
import DynamicIcon from "components/common/DynamicIcon"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { countryMap } from "helpers/countryMapping"
import Button from "components/Button"
import Alert from "components/Alert"
import { baseStyles } from "styles/base"
// import Modal from "components/Modal"
import { ButtonLink } from "components"
import { country } from "App"
import { unauthUser } from "store/actions/authActions"

const StyledTab = styled(Tabs)`
	&& {
		.ant-tabs-left-content {
			padding-left: 4em;
		}
	}
`

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 }
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 14 }
	}
}

const tailLayout = {
	wrapperCol: {
		xs: {
			span: 24,
			offset: 0
		},
		sm: {
			span: 16,
			offset: 6
		}
	}
}

const programmeClient = newClient("programme")
const client = newClient("account")

function Settings({ i18n }) {
	const [userProfile, setUserProfile] = useState({})
	const [isDangerActive, setIsDangerActive] = useState(false)
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}
	const isStudent = (userDetails.role || {}).code === "STDN"

	const { push } = useHistory()

	const { firstName, lastName, dob, phone = "", race, gender } = userProfile
	const initialPhoneNumber = phone && phone.startsWith("+") ? phone.slice(3) : phone
	const initials = { firstName, lastName, dob, phone: initialPhoneNumber, race, gender }
	const curriculumList = userProfile.curriculumList || []
	const curriculum = (curriculumList && curriculumList[0] && curriculumList[0].source) || {}

	const HAVE_SPECIFIC_REASON = i18n._(t`I have specific reason`)

	const reasonOptions = [
		{ value: "I registered with wrong role", label: i18n._(t`I registered with wrong role`) },
		{ value: "I have no longer been active in education", label: i18n._(t`I have no longer been active in education`) },
		{ value: HAVE_SPECIFIC_REASON, label: HAVE_SPECIFIC_REASON }
	]

	const { data = {} } = useQuery(queryFetchLevels, {
		client: programmeClient,
		variables: { country: userDetails.country }
	})

	const currOptions = (data.getProgrammeLevelList || []).map(item => ({
		value: Number(item.pvid),
		label: item.name
	}))

	const [signoutUser] = useLazyQuery(queryLogout, unauthUser)

	const [deactivateAccount] = useMutation(MUTATE_DEACTIVATE_ACCOUNT, {
		client,
		onCompleted: data => {
			signoutUser()
		}
	})

	const [fetchUser, { loading: loadingFetch }] = useMutation(mutateUserProfile, {
		client: newClient(),
		onCompleted: data => {
			data = data.getProfile || {}
			setUserProfile(data)
		}
	})

	const [updateCurriculum] = useMutation(mutateUpdateCurriculum, {
		client: newClient("profile"),
		onCompleted: () => {
			message.success(i18n._(t`Your curriculum has been updated`))
			fetchUser()
		}
	})

	const [updateProfile] = useMutation(mutateUpdateProfile, {
		client: newClient("account"),
		onCompleted: () => message.success(i18n._(t`Your profile has been updated`)),
		onError: err => renderError(err)
	})

	const [changePassword] = useMutation(mutateUpdatePassword, {
		client: newClient("account"),
		onCompleted: () => {
			message.loading(i18n._(t`Resetting your password...`)).then(() => {
				message.success(i18n._(t`Your password has been changed`))
			})
		},
		onError: err => renderError(err)
	})

	const handleUpdateProfile = (values, { setSubmitting }) => {
		values = { ...values, dob: moment(values.dob).format("YYYY-MM-DD") }
		updateProfile({ variables: values }).finally(() => setSubmitting(false))
	}

	const handleChangePassword = (values, { setSubmitting }) => {
		changePassword({ variables: values }).finally(() => setSubmitting(false))
	}

	const handleUpdateCurriculum = (values, { setSubmitting }) => {
		values = curriculumList.length > 0 ? { pvid: curriculumList[0].pvid, ...values } : values
		updateCurriculum({ variables: { list: [{ ...values }] } }).finally(() => {
			setSubmitting(false)
		})
	}

	const handleDeactivate = (values, { setSubmitting }) => {
		const { reason, another_reason } = values
		Modal.confirm({
			width: 600,
			centered: true,
			title: i18n._(t`Final warning`),
			content: i18n._(t`This action will deactivate your Cudy account forever. Are you sure you want to do this?`),
			onOk: () => deactivateAccount({ variables: { reason: another_reason || reason } }),
			okText: i18n._(t`Yes, I'd like to delete my account now`),
			cancelText: i18n._(t`No, cancel this`)
		})
		setSubmitting(false)
	}

	const roleIcon = () => {
		let icon = ""
		if (userProfile.gender === "F".toUpperCase()) {
			if (userProfile.roleCode === "STDN") icon = "icon--student"
			else icon = "icon--businesswoman"
		}
		if (userProfile.gender === "M".toUpperCase()) {
			if (userProfile.roleCode === "STDN") icon = "icon--basicpeople"
			else icon = "icon--businessman"
		}
		return icon
	}

	const iconForRole = roleIcon()

	useEffect(() => {
		fetchUser()
	}, [fetchUser, userProfile.gender, userProfile.roleCode])

	const isStillOnProgress = false

	if (!localStorage.getItem("token")) push(`/${country}/404`)

	if (loadingFetch) return <Loading />

	return (
		<Layout breadcrumb={false}>
			{/* <Modal footer={false} visible={deactivateModal}>
				<Heading
					level={4}
					marginBottom="3em"
					content="Final warning"
					subheader="This action will deactivate your Cudy account forever. Are you sure you want to do this?"
				/>
				<Button type="primary">
					<Trans>Yes, delete my account</Trans>
				</Button>{" "}
				&nbsp;{" "}
				<ButtonLink onClick={() => setDeactivateModal(false)}>
					<Trans>Cancel</Trans>
				</ButtonLink>
			</Modal> */}

			<Section paddingHorizontal="very" style={{ paddingTop: "4em" }}>
				<Row type="flex" justify="center" className="mb4em">
					<Col lg={18}>
						<StyledTab tabPosition={mobile ? "top" : "left"}>
							<Tabs.TabPane tab={<Trans>Basic</Trans>} key="basic">
								<Row type="flex" align="middle" justify="space-between" style={{ marginBottom: "2em" }}>
									<Col lg={8}>
										<Heading
											content={<Trans>Basic</Trans>}
											level={4}
											subheader={<Trans>Your basic profile details</Trans>}
										/>
									</Col>
									<Col lg={8}>
										<Trans>
											<DynamicIcon type={iconForRole} /> &nbsp; You are a{" "}
											<strong className="primary">{userProfile.roleName}</strong>
										</Trans>
									</Col>
								</Row>
								<Formik enableReinitialize onSubmit={handleUpdateProfile} initialValues={initials}>
									{({ handleSubmit, dirty }) => (
										<Form {...formItemLayout} onSubmit={handleSubmit}>
											<TextInput
												name="firstName"
												placeholder={i18n._(t`First name`)}
												label={<Trans>First name</Trans>}
											/>
											<TextInput
												name="lastName"
												placeholder={i18n._(t`Last name`)}
												label={<Trans>Last name</Trans>}
											/>
											<RadioInput
												name="gender"
												label={<Trans>Gender</Trans>}
												options={[
													{ value: "M", label: <Trans>Male</Trans> },
													{ value: "F", label: <Trans>Female</Trans> }
												]}
											/>
											<FormItem name="dob" label={<Trans>DOB</Trans>}>
												<DatePicker
													name="dob"
													placeholder={i18n._(t`Your date of birth`)}
													label={<Trans>DOB</Trans>}
												/>
											</FormItem>
											{/* <SelectInput
												name="race"
												placeholder={i18n._(t`Race`)}
												label={<Trans>Race</Trans>}
												options={races}
											/> */}
											<TextInput
												name="phone"
												placeholder={i18n._(t`Mobile number`)}
												label={<Trans>Mobile number</Trans>}
												addonBefore={(countryMap[userDetails.country] || {}).phoneCode}
												marginBottom="3em"
											/>
											<Form.Item {...tailLayout}>
												<Trans>
													<SubmitButton size="large" disabled={!dirty}>
														Save changes
													</SubmitButton>
												</Trans>
											</Form.Item>
										</Form>
									)}
								</Formik>
							</Tabs.TabPane>

							{isStudent && (
								<Tabs.TabPane tab={<Trans>Education</Trans>} key="education">
									<Heading
										content={<Trans>Education</Trans>}
										level={4}
										subheader={<Trans>Some educational details of you</Trans>}
										marginBottom="3em"
									/>
									<Formik initialValues={{ programmeCurriculumPvid: curriculum.name }} onSubmit={handleUpdateCurriculum}>
										{({ handleSubmit, dirty }) => (
											<Form {...formItemLayout} onSubmit={handleSubmit}>
												<SelectInput
													name="programmeCurriculumPvid"
													label={<Trans>Curriculum and level</Trans>}
													placeholder={i18n._(t`Select one...`)}
													options={currOptions}
												/>
												<Form.Item {...tailLayout}>
													<Trans>
														<SubmitButton disabled={!dirty} size="large">
															Save changes
														</SubmitButton>
													</Trans>
												</Form.Item>
											</Form>
										)}
									</Formik>
								</Tabs.TabPane>
							)}

							<Tabs.TabPane tab={<Trans>Change password</Trans>} key="password">
								<Heading
									content={<Trans>Change password</Trans>}
									level={4}
									subheader={<Trans>If you don't satisfy with your current password</Trans>}
									marginBottom="3em"
								/>
								<Formik onSubmit={handleChangePassword} validate={validate} initialValues={{}}>
									{({ handleSubmit, dirty }) => (
										<Form {...formItemLayout} onSubmit={handleSubmit}>
											<TextInput
												password
												name="newPassword1"
												placeholder={i18n._(t`Your new password...`)}
												label={<Trans>Your new password</Trans>}
											/>
											<TextInput
												password
												name="newPassword2"
												placeholder={i18n._(t`Repeat your new password...`)}
												label={<Trans>Repeat password</Trans>}
											/>
											<TextInput
												password
												name="oldPassword"
												placeholder={i18n._(t`Your old password...`)}
												label={<Trans>Your old password</Trans>}
											/>
											<Form.Item {...tailLayout}>
												<SubmitButton size="large" disabled={!dirty}>
													<Trans>Save changes</Trans>
												</SubmitButton>
											</Form.Item>
										</Form>
									)}
								</Formik>
							</Tabs.TabPane>

							{!isStillOnProgress && (
								<Tabs.TabPane
									tab={
										<Trans>
											<Icon type="warning" theme="filled" style={{ color: baseStyles.redColor }} /> Danger zone
										</Trans>
									}
									key="danger"
								>
									<Heading
										content={<Trans>Danger zone</Trans>}
										level={4}
										subheader={
											<Trans>
												If for some reasons you would like to delete your Cudy account, you can use this section
											</Trans>
										}
										marginBottom="3em"
									/>
									{isDangerActive ? (
										<Formik onSubmit={handleDeactivate} initialValues={{ reason: "" }}>
											{({ handleSubmit, dirty, values }) => (
												<Form layout="vertical" onSubmit={handleSubmit}>
													<SelectInput
														name="reason"
														options={reasonOptions}
														label={<Trans>Tell us the reason</Trans>}
													/>
													{values.reasons === HAVE_SPECIFIC_REASON && (
														<TextInput
															texarea
															rows={3}
															name="another_reason"
															placeholder={i18n._(t`Write down your specific reason...`)}
														/>
													)}
													<SubmitButton type="primary" size="large" disabled={!dirty}>
														<Trans>Delete my account</Trans>
													</SubmitButton>{" "}
													&nbsp;
													<ButtonLink onClick={() => setIsDangerActive(false)}>
														<Trans>Cancel</Trans>
													</ButtonLink>
												</Form>
											)}
										</Formik>
									) : (
										<>
											<Alert
												message={<Trans>Delete your account?</Trans>}
												description={<Trans>Only proceed if you are completely sure of what you are doing</Trans>}
												type="warning"
												showIcon
											/>
											<Button type="primary" onClick={() => setIsDangerActive(true)}>
												<Trans>
													Proceed <Icon type="right" />{" "}
												</Trans>
											</Button>
										</>
									)}
								</Tabs.TabPane>
							)}
						</StyledTab>
					</Col>
				</Row>
			</Section>
		</Layout>
	)
}

const validate = values => {
	const error = {}

	if (!values.newPassword1) error.newPassword1 = "You have to provide your new password"
	else if (values.newPassword1.length < 8) error.newPassword1 = "At least 8 characters"
	if (!values.newPassword2) error.newPassword2 = "Please repeat your new password"
	else if (values.newPassword2 !== values.newPassword1) error.newPassword2 = "The password doesn't match"

	return error
}

export default withI18n()(withRouter(Settings))
